import { Switch, Route } from "react-router-dom";

import CvMaker from "./component/cv-maker";
import AboutME from "./component/about-me";

function App() {
  return (
      <Switch>
        <Route path='/' exact >
          <CvMaker />
        </Route>
        <Route path='/about-me'>
          <AboutME />
        </Route>
      </Switch>
  );
}

export default App;
